.Toolbar {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #ffb657;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    box-sizing: border-box;
    z-index: 90;
}

/* For MOBILE Devices */
@media (max-width: 499px) {
    .DesktopOnly {
        display: none;
    }
}